.battleDetail{
  .battleCard{
    margin-top: 0.1rem;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -moz-box-align: stretch;
     -ms-flex-align: stretch;
         align-items: stretch;
    -ms-flex-pack: distribute;
        justify-content: space-around;

    .battleCard-item{
      width: 2.64rem;
      max-width: 264Px;
      background-color: #dddddd;
      padding: 32Px;
      .battleCard-item-title{
        width: 100%;
        text-align: center;
        font-weight: bold;
        padding: 16Px 0;
        font-size: 0.2rem;
      }
      .battleCard-item-img{
        width: 100%;
      }
      .battleCard-item-scores{
        font-weight: bold;
        font-size: 0.12rem;
        text-align: center;
        margin-top: 0.1rem;
      }
      .battleCard-item-name{
        color: #1890ff;
        font-weight: bold;
        font-size: 0.12rem;
        text-align: center;
      }
      .battleCard-item-row{
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-align: center;
         -ms-flex-align: center;
             align-items: center;
        font-size: 0.08rem;
        .row-icon{
          margin-right: 0.04rem;
          font-size: 16Px;
        }
      }
    }
  }
  .reset-button{
    margin-top: 16Px;
    margin-bottom: 16Px;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -moz-box-align: center;
     -ms-flex-align: center;
         align-items: center;
    -moz-box-pack: center;
     -ms-flex-pack: center;
         justify-content: center;
    width: 100%;
  }
}